<template>
  <div id="app" :class="{bg:!isLoginOk}">
    <!-- *******  ******* -->
    <div v-if="!isLoginOk" class="login-box" @keyup.enter="ajaxLogin">
      <h1><span class="hks">HKS</span> <span></span></h1>
      <el-input v-model="loginName" placeholder="" autofocus></el-input>
      <el-input v-model="loginPassword" placeholder="" type="password"></el-input>
      <el-button type="primary" @click.prevent="ajaxLogin"></el-button>
    </div>

    <!-- *******  ******* -->
    <div v-else>
    <header>
      <div class="header-container">
        <a href="javascript:;" class="logo">
          <img src="./assets/imgs/logo.png" />
          <span></span>
        </a>
      <div class="account">
        <div class="account-info">
          <p class="login-out" @click="loginOut"></p>
          <p class="companyname">{{userInfo.truename}}</p>
        </div>
      </div>
        <menu class="clrfix">
          <router-link v-for="m in menu"  :to="{name: m.name}" :key="m.name"  :class="{'router-link-active':$route.path==m.path}">{{m.txt}}</router-link>
        </menu>
      </div>
    </header>

    <!-- content -->
    <div id="content">
      <p class="current-page"><span @click="breadFn" class="bread-home">{{firstMenuActive}}</span> <span class="bread-sub" v-if="secondMenuActive"> / {{secondMenuActive}}</span></p>
      <!--  -->
      <!--<transition>-->
        <router-view></router-view>
      <!--</transition>-->
    </div>
    </div>
  </div>
</template>

<script>
  import {encryptDES} from './assets/js/des'
  import {mapState,mapMutations} from 'vuex'
export default {
  name: 'app',
  data() {
    return {
      loginName:"",
      loginPassword:"",
      isLoginOk:false,        //
//      firstMenuActive:"", //
//      secondMenuActive:"",    //
      userInfo:{},
      menu: [
        {
          name: "price",
          path:"/price",
          txt: "",
        }, {
          name: "order",
          path:"/order",
          txt: ""
        }, {
          name: "rule",
          path:"/rule",
          txt: ""
        }, {
          name: "supplier",
          path:"/supplier",
          txt: ""
        }, {
          name: "setting",
          path:"/setting",
          txt: ""
        }
      ],
      list: ""
    }
  },
  computed:{
    ...mapState([
        'firstMenuActive','secondMenuActive'
    ])
  },
  watch: {
    '$route'(to, from) {
      this.funSetFirstMenu(to.name)
      this.setSecondMenu('')
    }
  },
  mounted() {
    if(window.sessionStorage.getItem('isLogin')){
      this.isLoginOk = true;
    }else{
      this.isLoginOk = false;
    }
    if(window.sessionStorage.getItem('userInfo')){
        this.userInfo=JSON.parse(window.sessionStorage.getItem('userInfo'))
    }
  },
  methods:{
    ...mapMutations([
        'setFirstMenu','setSecondMenu','setTagName'
    ]),
    breadFn(){
      switch (this.firstMenuActive) {
        case "":
          this.$router.push({path:'/price'})
          break;
        case "":
          this.$router.push({path:'/order'})
          break;
        case "":
          this.$router.push({path:'/rule'})
          break;
        case "":
          this.$router.push({path:'/supplier'})
          break;
        case "":
          this.$router.push({path:'/setting'})
          break;
        default:
          this.$router.push({path:'/price'})
          break;
      }
    },
    loginOut(){
      window.sessionStorage.removeItem('isLogin')
      window.sessionStorage.removeItem('userInfo')
      this.$router.push({path: "/"});//
      this.setTagName('first');//
      this.isLoginOk = false
      this.loginName=''
      this.loginPassword=''
    },
    ajaxLogin() {
      //
      let verifysign = "{\"username\":\"" + this.loginName + "\",\"password\":\"" + this.loginPassword + "\"}"; //
      verifysign = encryptDES(verifysign);
      this.$http.post("UserLogin.aspx", {
        usertype: "sysuser",          //
        verifysign: verifysign    // json
      }).then((res) => {
        //
        if(res.body.success){
          this.userInfo = res.body;   //
          this.isLoginOk = true;    //
          window.sessionStorage.setItem('isLogin',JSON.stringify(this.isLoginOk))
          window.sessionStorage.setItem('userInfo',JSON.stringify(this.userInfo))
        }else{
          this.$message.error(res.body.msg);
        }
      })
    }
    ,
    funSetFirstMenu(routename){
      //
       let txt;
       switch (routename) {
        case "price":
          txt = "";
          break;
        case "order":
          txt = "";
          break;
        case "rule":
          txt = "";
          break;
       case "supplier":
         txt = "";
         break;
       case "setting":
         txt = "";
         break;
        default:
          txt = "";
          break;
      }
      this.setFirstMenu(txt)
    }
  }
}
</script>

<style>
* {
  padding: 0;
  margin: 0;
  font: 14px "";
}

body,
html,
#app,
.content,
.menu {
  height: 100%;
}

body,
html {
  overflow-x: hidden;
}

body {
  background: #fff;
}
input:-webkit-autofill { box-shadow: 0 0 0px 1000px white inset !important;}
.bread-sub{
  color: #97a8be;
  cursor: text;
}
.bread-home{
  transition: color .15s linear;
  color: #48576a;
}
.bread-home:hover{
  color: #20a0ff;
  cursor: pointer;
}
.clrfix::after{
  content:"";
  display:block;
  clear:both;
  visibility:hidden;
  height:0;
  line-height:0;
  font-size:0;

}
.flex-wrap {
  display: flex;
}

.flex-item {
  flex: 1;
}

#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}

a {
  text-decoration: none;
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-to /* .fade-leave-active in below version 2.1.8 */ {
  opacity: 0
}
.bg{background:#f3f5f6;}
.login-box{height:300px;width:400px;position:fixed;top:50%;left:50%;transform:translate(-50%,-50%);}
.login-box h1{font-size:24px;font-weight:bold;color:#20a0ff;text-align:center;margin-bottom:40px;}
.login-box h1 span{color:#97a8be;font-size:15px;padding-left:10px;position:relative;top:2px;}
.login-box input{height:60px;margin-bottom:10px;font-size:16px;}
.login-box button{width:100%;margin-top:15px;height:60px;line-height:60px;padding:0;}
.login-box button span{font-size:16px;}
header {
  height: 120px;
  color: #fff;
  position: relative;
  width: 100%;
  background: #20a0ff;
}

header .header-container {
  height: 120px;
  width: 1200px;
  margin: 0 auto;
  position: relative;
}

header a.logo {
  color: #fff;
}

header a.logo img {
  float:left;
  margin: 28px 10px 10px 0;
}

header a.logo span {
  font-size: 24px;
  font-weight:bold;
  float:left;
  margin-top:61px;
  font-family: cursive;
}
.login-box h1 .hks{
  color: #20a0ff;
  font-size: 24px;
  margin-right: -2px;
  font-family: unset;
}
header .account{float:right;padding-top:10px;}
.account-info{float:right;text-align:right;}
.account-info .name{font-size:12px;}
.account-info .companyname{font-size:15px;margin-top:20px;}
.account-headpic{float:right;width:60px;height:60px;border:1px solid #ccc;border-radius:100%;margin-left:10px;}

menu {
 position: absolute;bottom:0;right:0;
}
.login-out{
  cursor: pointer;
}
menu a {
  float: left;
  margin-left: 20px;
  padding: 5px 20px 10px;
  opacity: .8;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  position:relative;
}
menu a.router-link-active {
  opacity:1;
  font-weight:bold;
  border-color:#fff;
}
menu a.router-link-active::after{content:"";position:absolute;bottom:0;left:0;right:0;height:4px;background:#99D2FF;}

menu a:hover {
   opacity:1;
}


#content{width:1200px;margin:12px auto;}
.current-page {
  font-size: 13px;
}
</style>



