<template>
  <div class="seeting-detail">
    <el-row class="">
      <el-col :span="6">
        <div class=" ">&nbsp;</div>
      </el-col>
      <el-col :span="12">
        <div class=" ">
          <div class=" "><h2></h2></div>
          <el-row class="info-item">
            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" class="demo-ruleForm">
              <el-form-item label="" prop="account">
                <el-row>
                  <el-col :span="10"><el-input v-model="ruleForm.account" auto-complete="off"></el-input></el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" prop="pass">
                <el-row>
                  <el-col :span="10"><el-input v-model="ruleForm.pass" auto-complete="off"></el-input></el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" prop="username">
                <el-row>
                  <el-col :span="10"><el-input v-model="ruleForm.username" auto-complete="off"></el-input></el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" prop="usertell">
                <el-row>
                  <el-col :span="10"><el-input v-model="ruleForm.usertell" auto-complete="off"></el-input></el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" >
                <el-row :gutter="5">
                  <el-col :span="6">
                    <el-form-item prop="province" >
                      <el-select v-model="ruleForm.province" @change="getCity" placeholder="" >
                        <el-option
                          v-for="item in provinceOptions"
                          :key="item.code"
                          :label="item.province"
                          :value="item.code">
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item prop="city">
                      <el-select v-model="ruleForm.city"  placeholder="" required>
                        <el-option
                          v-for="item in cityOptions"
                          :key="item.code"
                          :label="item.city"
                          :value="item.city">
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="10"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" >
                <el-row>
                  <el-col :span="10">
                    <el-form-item prop="company">
                      <el-select v-model="ruleForm.company" placeholder="" required>
                        <el-option
                          v-for="item in companyOptions"
                          :key="item.value"
                          :label="item.text"
                          :value="item.value">
                        </el-option>
                      </el-select>
                    </el-form-item>
                  </el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" >
                <el-row>
                  <el-col :span="10">
                    <el-select v-model="ruleForm.rule" placeholder=""  >
                      <el-option
                        v-for="item in ruleOptions"
                        :key="item.value"
                        :label="item.text"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-col>
                  <el-col :span="14"><div class="tip"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item label="" >
                <el-row>
                  <el-col :span="10">
                    <el-select v-model="ruleForm.status" placeholder="">
                      <el-option
                        v-for="item in stateOptions"
                        :key="item.value"
                        :label="item.text"
                        :value="item.value">
                      </el-option>
                    </el-select>
                  </el-col>
                  <el-col :span="14"><div class="tips"></div></el-col>
                </el-row>
              </el-form-item>
              <el-form-item>
                <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
                <el-button @click="resetForm('ruleForm')"></el-button>
              </el-form-item>
            </el-form>
          </el-row>
        </div>
      </el-col>
      <el-col :span="6">
        <div class=" ">&nbsp;</div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import {mapState,mapMutations} from "vuex"
  import ElRow from "element-ui/packages/row/src/row";
  import ElCol from "element-ui/packages/col/src/col";
  export default{
    components: {
      ElCol,
      ElRow
    },
    data(){
      var checkAccount= (rule,value,callback)=>{
          if (value === '') {
            callback(new Error(''));
          }else if(!/^[0-9a-zA-Z]*$/.test(value)){
            callback(new Error(''));
          } else{
            callback()
          }
      }
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else if(!/^\w+$/.test(value)){
          callback(new Error(''));
        } else if(value.length<6){
          callback(new Error('6'));
        }
      };
      var checkName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }
      };
      var checkTell= (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        }else if(!/^1\d{10}$/.test(value)){
          callback(new Error(''));
        }
      };
      return {
        provinceOptions:[],
        cityOptions:[],
        companyOptions:[
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          }
        ],
        ruleOptions:[
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          }
        ],
        stateOptions:[
          {
            "value": "",
            "text": ""
          },
          {
            "value": "",
            "text": ""
          }
        ],
        ruleForm: {
          pass: '',
          account:'',
          username:'',
          usertell:'',
          province:"",
          city:"",
          rule:'',
          company:'',
          status:''
        },
        rules: {
          pass: [
            { validator: validatePass, trigger: 'blur' }
          ],
          account:[
            { validator: checkAccount, trigger: 'blur' }
          ],
          username:[
            {validator:checkName,trigger:'blur'}
          ],
          usertell:[
            {validator:checkTell,trigger:'blur'}
          ],
          province:[
            { required: true, message: '', trigger: 'change' }
          ],
          city:[
            { required: true, message: '', trigger: 'change' }
          ],
          company:[
            { required: true, message: '', trigger: 'change' }
          ]
        }
      }
    },
    computed:{
      ...mapState(
          ['settingDetail']
      )
    },
    methods:{
      ...mapMutations(
          ['setFirstMenu','setSecondMenu']
      ),
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {})
        if(!/^[0-9a-zA-Z]*$/.test(this.ruleForm.account)){
            return false
        }
        if(!/^\w+$/.test(this.ruleForm.pass) || this.ruleForm.pass.length<6){
            return false
        }
        if(!/^1\d{10}$/.test(this.ruleForm.usertell)){
            return false
        }
        for(var key in this.ruleForm){
           if(!this.ruleForm[key]){
               return
           }
        }
        this.provinceOptions.forEach((item)=>{
            if(item.code==this.ruleForm.province){
                this.ruleForm.province=item.province
            }
        })
        console.log(this.ruleForm)
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
        if(this.$route.query.userid!="0"){
          this.$http.post('/user',{
            userid:this.$route.query.userid
          }).then((res)=>{
            this.ruleForm=JSON.parse(res.body)
          })
        }else{
          this.ruleForm= {
            pass: '',
            account:'',
            username:'',
            usertell:'',
            province:"",
            city:"",
            rule:'',
            company:'',
            status:''
          }
        }
      },
      getCity(val){
        var value
        if(isNaN(parseInt(val))){
          setTimeout(()=>{
            this.provinceOptions.forEach((item)=>{
              if(item.province==val){
                value=item.code
              }
            })
            this.$http.post('/city',{
              'code':value
            }).then((res)=>{
              this.cityOptions=JSON.parse(res.body)
            })
          },100)
        }else{
          value=val
          this.$http.post('/city',{
            'code':value
          }).then((res)=>{
            this.cityOptions=JSON.parse(res.body)
          })
        }

      }
    },
    mounted(){
      this.setFirstMenu("")
      if(this.$route.query.userid!="0"){
        console.log(this.$route.query.userid)
        this.$http.post('/user',{
          userid:this.$route.query.userid
        }).then((res)=>{
          this.ruleForm=JSON.parse(res.body)
        })
        this.setSecondMenu("")
      }else{
        this.setSecondMenu("")
      }
//      this.$http.post('/company').then((res)=>{
//          this.companyOptions=JSON.parse(res.body)
//      })
      this.$http.post('/province').then((res)=>{
        this.provinceOptions=JSON.parse(res.body)
      })
    }
  }
</script>

<style>
  h2{
    display: inline-block;
    font-weight: bold;
    font-size: 16px;
    border-left: 4px solid #20a0ff;
    padding: 5px 10px;
  }
  .tips{
    line-height: 36px;
    text-indent: 20px;
    color: #666666;
  }
  .tip{
    line-height: 20px;
    margin-left: 20px;
    color: #666666;
  }
  .seeting-detail{
    margin-top: 20px;
  }
</style>



